import React, { useEffect, useState, useRef } from "react";
import { Panel, FlexboxGrid, IconButton, Tag } from "rsuite";
import "./queueStyles.css";

function VideoSectionAuto({
  videos,
  initialVideoIndex = 0,
  videoExpanded,
  setVideoExpanded,
}) {
  const [currentVideo, setCurrentVideo] = useState(initialVideoIndex);
  const [isPlaying, setIsPlaying] = useState(true);
  const videoRef = useRef(null);
  const youtubePlayerRef = useRef(null);
  const timerRef = useRef(null);
  const tempDivRef = useRef(null);
  const bannerDisplayTime = 5000; // 5 secondes pour les bannires

  // Gestion du changement de contenu
  const nextVideo = () => {
    setCurrentVideo((prev) => (prev + 1) % videos.length);
  };

  // Fonction pour dmarrer la lecture d'une vido
  const playVideo = () => {
    if (videoRef.current) {
      videoRef.current
        .play()
        .catch((e) => console.error("Erreur de lecture automatique:", e));
      setIsPlaying(true);
    }
  };

  // Fonction pour extraire l'ID YouTube d'une URL
  const getYoutubeIdFromUrl = (url) => {
    const regExp =
      /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
    const match = url.match(regExp);
    return match && match[2].length === 11 ? match[2] : null;
  };

  // Initialisation de l'API YouTube Player
  useEffect(() => {
    // Chargement de l'API YouTube IFrame si elle n'est pas dj charge
    if (!window.YT) {
      const tag = document.createElement("script");
      tag.src = "https://www.youtube.com/iframe_api";

      window.onYouTubeIframeAPIReady = () => {
        console.log("YouTube API ready");
      };

      const firstScriptTag = document.getElementsByTagName("script")[0];
      firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);
    }
  }, []);

  // Effet pour grer le dfilement automatique des contenus
  useEffect(() => {
    const video = videos[currentVideo];

    // Nettoyer tout timer existant
    if (timerRef.current) {
      clearTimeout(timerRef.current);
      timerRef.current = null;
    }

    // Nettoyer le div temporaire s'il existe
    if (tempDivRef.current && document.body.contains(tempDivRef.current)) {
      document.body.removeChild(tempDivRef.current);
      tempDivRef.current = null;
    }

    // Configuration du timer en fonction du type de contenu
    if (video.type === "Bannire") {
      // Pour les bannires, attendre 5 secondes puis passer  la suivante
      timerRef.current = setTimeout(() => {
        nextVideo();
      }, bannerDisplayTime);
    } else if (video.type === "local") {
      // Pour les vidos locales
      if (videoRef.current) {
        // Lancer la lecture automatique
        videoRef.current.play().catch((e) => {
          console.error("Erreur de lecture automatique:", e);
          // Si la lecture automatique choue, on passe au contenu suivant aprs un dlai
          timerRef.current = setTimeout(nextVideo, 2000);
        });

        // Obtenir la dure de la vido une fois que les mtadonnes sont charges
        const handleMetadataLoaded = () => {
          if (videoRef.current) {
            const videoDuration = videoRef.current.duration * 1000; // Convertir en millisecondes
            console.log(`Dure de la vido locale: ${videoDuration}ms`);

            // Configurer le timer pour passer  la vido suivante  la fin de la dure
            timerRef.current = setTimeout(() => {
              nextVideo();
            }, videoDuration);
          }
        };

        // Ajouter l'couteur pour l'vnement loadedmetadata
        videoRef.current.addEventListener(
          "loadedmetadata",
          handleMetadataLoaded
        );

        // Nettoyer l'couteur lors du dmontage
        return () => {
          if (videoRef.current) {
            videoRef.current.removeEventListener(
              "loadedmetadata",
              handleMetadataLoaded
            );
          }
          if (timerRef.current) {
            clearTimeout(timerRef.current);
          }
        };
      }
    } else if (video.type === "youtube") {
      // Pour les vidos YouTube, utiliser l'API YouTube IFrame
      if (window.YT && window.YT.Player) {
        // Extraire l'ID de la vido YouTube  partir de l'URL
        const youtubeId = getYoutubeIdFromUrl(video.source);

        if (youtubeId) {
          try {
            // Crer une div temporaire pour le player YouTube (elle sera remplace par l'iframe)
            const tempYoutubeDiv = document.createElement("div");
            tempYoutubeDiv.id = "temp-youtube-player";
            tempYoutubeDiv.style.display = "none";
            document.body.appendChild(tempYoutubeDiv);
            tempDivRef.current = tempYoutubeDiv; // Stocker la rfrence pour le nettoyage ultrieur

            // Initialiser le player YouTube
            youtubePlayerRef.current = new window.YT.Player(
              "temp-youtube-player",
              {
                videoId: youtubeId,
                events: {
                  onReady: (event) => {
                    // Obtenir la dure de la vido YouTube en secondes
                    const duration = event.target.getDuration() * 1000; // Convertir en millisecondes
                    console.log(`Dure de la vido YouTube: ${duration}ms`);
                    // Configurer le timer pour passer  la vido suivante 1 seconde avant la fin
                    timerRef.current = setTimeout(() => {
                      nextVideo();
                    }, duration);
                    console.log(
                      `Dure de la vido YouTube------->: ${duration - 3000}ms`
                    );

                    // Nettoyer le player temporaire seulement s'il existe encore dans le DOM
                    if (
                      tempDivRef.current &&
                      document.body.contains(tempDivRef.current)
                    ) {
                      document.body.removeChild(tempDivRef.current);
                      tempDivRef.current = null;
                    }
                  },
                  onError: (event) => {
                    console.error("Erreur YouTube Player:", event);
                    // En cas d'erreur, passer  la vido suivante aprs un dlai
                    timerRef.current = setTimeout(nextVideo, 2000);

                    // Nettoyer le player temporaire seulement s'il existe encore dans le DOM
                    if (
                      tempDivRef.current &&
                      document.body.contains(tempDivRef.current)
                    ) {
                      document.body.removeChild(tempDivRef.current);
                      tempDivRef.current = null;
                    }
                  },
                },
              }
            );
          } catch (error) {
            console.error(
              "Erreur lors de l'initialisation du player YouTube:",
              error
            );
            // En cas d'erreur, utiliser un dlai par dfaut
            timerRef.current = setTimeout(nextVideo, 30000);

            // Nettoyer le player temporaire en cas d'erreur
            if (
              tempDivRef.current &&
              document.body.contains(tempDivRef.current)
            ) {
              document.body.removeChild(tempDivRef.current);
              tempDivRef.current = null;
            }
          }
        } else {
          console.error("ID YouTube invalide pour:", video.source);
          // En cas d'ID YouTube invalide, utiliser un dlai par dfaut
          timerRef.current = setTimeout(nextVideo, 30000);
        }
      } else {
        console.warn(
          "API YouTube pas encore charge, utilisation d'un dlai par dfaut"
        );
        // Si l'API YouTube n'est pas encore charge, utiliser un dlai par dfaut
        timerRef.current = setTimeout(() => {
          nextVideo();
        }, 30000);
      }
    }

    // Nettoyage  la dsinstallation du composant ou au changement de vido
    return () => {
      if (timerRef.current) {
        clearTimeout(timerRef.current);
        timerRef.current = null;
      }

      // Nettoyer le div temporaire s'il existe
      if (tempDivRef.current && document.body.contains(tempDivRef.current)) {
        document.body.removeChild(tempDivRef.current);
        tempDivRef.current = null;
      }

      // Nettoyer le player YouTube s'il existe
      if (youtubePlayerRef.current) {
        try {
          youtubePlayerRef.current.destroy();
        } catch (e) {
          console.error("Erreur lors de la destruction du player YouTube:", e);
        }
        youtubePlayerRef.current = null;
      }
    };
  }, [currentVideo, videos]);

  // Effet spar pour grer l'vnement 'ended' des vidos locales
  useEffect(() => {
    // Ne s'applique qu'aux vidos locales
    if (videos[currentVideo].type !== "local") return;

    // Fonction pour passer  la vido suivante quand celle-ci est termine
    const handleVideoEnd = () => {
      nextVideo();
    };

    // Ajouter l'couteur d'vnement
    const currentVideoElement = videoRef.current;
    if (currentVideoElement) {
      currentVideoElement.addEventListener("ended", handleVideoEnd);
    }

    // Nettoyer l'couteur d'vnement quand le composant est dmont ou quand la vido change
    return () => {
      if (currentVideoElement) {
        currentVideoElement.removeEventListener("ended", handleVideoEnd);
      }
    };
  }, [currentVideo]); // Cette dpendance assure que l'effet est rexcut  chaque changement de vido

  const renderVideoContent = () => {
    const video = videos[currentVideo];

    const opts = {
      height: "500",
      width: "100%",
      playerVars: {
        autoplay: 1,
        controls: 1,
        modestbranding: 1,
        rel: 0,
      },
    };

    if (video.type === "youtube") {
      // Ajouter les paramtres autoplay=1 et enablejsapi=1 pour l'API YouTube
      const youtubeId = getYoutubeIdFromUrl(video.source);
      const youtubeEmbedUrl = `https://www.youtube.com/embed/${youtubeId}?autoplay=1&enablejsapi=1`;

      return (
        <iframe
          src={`${youtubeEmbedUrl}${
            youtubeEmbedUrl.includes("?") ? "&" : "?"
          }mute=1`}
          title={video.title}
          className="video-frame"
          frameBorder="0"
          height="500px"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        />
        // <iframe
        //     src={youtubeEmbedUrl}
        //     title={video.title}
        //     className="video-frame"
        //     frameBorder="0"
        //     height="500px"
        //     allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        //     allowFullScreen
        // />
      );
    } else if (video.type === "Bannire") {
      return (
        <div className="banner-container">
          <img src={video.source} alt={video.title} className="banner-image" />
        </div>
      );
    } else {
      return (
        <video
          ref={videoRef}
          src={video.source}
          className="video-frame"
          poster="/api/placeholder/640/360"
          preload="auto"
        />
      );
    }
  };

  const currentVideoItem = videos[currentVideo];
  const showPlayButton = currentVideoItem.type !== "Bannire";

  return (
    <Panel shaded bodyFill className="video-container">
      {renderVideoContent()}
      <div className="video-overlay">
        <FlexboxGrid justify="space-between" align="bottom">
          <FlexboxGrid.Item>
            <h3 className="video-title">{currentVideoItem.title}</h3>
            <FlexboxGrid align="middle">
              {showPlayButton && (
                <IconButton
                  icon={
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="16"
                      height="16"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    >
                      <polygon points="5 3 19 12 5 21 5 3" />
                    </svg>
                  }
                  circle
                  size="sm"
                  className="play-button"
                  onClick={playVideo}
                />
              )}
              <Tag
                color={currentVideoItem.type === "Bannire" ? "green" : "blue"}
                className="content-tag"
              >
                {currentVideoItem.type === "youtube"
                  ? "YouTube"
                  : currentVideoItem.type === "Bannire"
                  ? "Bannire"
                  : "Vido"}
              </Tag>
            </FlexboxGrid>
          </FlexboxGrid.Item>
          <FlexboxGrid.Item>
            <IconButton
              icon={
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="18"
                  height="18"
                  viewBox="0 0 24 24"
                  fill="none"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  className={videoExpanded ? "rotate-180" : ""}
                >
                  <polyline points="9 18 15 12 9 6" />
                </svg>
              }
              circle
              size="sm"
              className="expand-button"
              onClick={() => setVideoExpanded(!videoExpanded)}
            />
          </FlexboxGrid.Item>
        </FlexboxGrid>
      </div>
    </Panel>
  );
}

export default VideoSectionAuto;
