import React, { useEffect, useRef, useState } from "react";
import { Panel, FlexboxGrid, IconButton } from "rsuite";
import { X, Bell } from "lucide-react";
import "./queueStyles.css"; // Importation des styles externaliss

function NotificationBar({ nextPatient, setShowNotification }) {
  // Rfrence  l'objet de synthse vocale
  const synth = useRef(null);
  const voiceRef = useRef(null);
  const [voicesLoaded, setVoicesLoaded] = useState(false);

  // Initialiser la synthse vocale immdiatement au montage du composant
  useEffect(() => {
    if (typeof window !== "undefined" && window.speechSynthesis) {
      synth.current = window.speechSynthesis;

      // Charger les voix disponibles
      const loadVoices = () => {
        const availableVoices = synth.current.getVoices();

        if (availableVoices.length > 0) {
          // Rechercher d'abord une voix Google franaise
          let voiceToUse = availableVoices.find(
            (v) =>
              v.name.toLowerCase().includes("google") && v.lang.includes("fr")
          );

          // Si aucune voix Google franaise n'est trouve, slectionner une voix franaise quelconque
          if (!voiceToUse) {
            voiceToUse = availableVoices.find((v) => v.lang.includes("fr"));
          }

          // Si toujours aucune voix franaise, prendre la voix par dfaut
          if (!voiceToUse && availableVoices.length > 0) {
            voiceToUse = availableVoices[0];
          }

          if (voiceToUse) {
            voiceRef.current = voiceToUse;
            console.log("Voix slectionne:", voiceToUse.name);
            setVoicesLoaded(true); // Marquer les voix comme charges
          }
        }
      };

      // Firefox et certains navigateurs peuvent avoir les voix dj charges
      loadVoices();

      // Chrome et autres navigateurs ncessitent d'attendre l'vnement voiceschanged
      synth.current.onvoiceschanged = () => {
        loadVoices();
        // Assurer que les voix sont marques comme charges
        setVoicesLoaded(true);
      };

      // Initialiser les voix mme si onvoiceschanged n'est pas dclench
      if (synth.current.getVoices().length > 0) {
        setVoicesLoaded(true);
      }
    }

    return () => {
      if (synth.current) {
        synth.current.onvoiceschanged = null;
        synth.current.cancel(); // Arrter la synthse vocale si le composant est dmont
      }
    };
  }, []);

  // Fonction pour lire le texte du ticket
  const speakTicketInfo = () => {
    if (!synth.current) {
      console.error("La synthse vocale n'est pas disponible");
      return;
    }

    // Arrter toute lecture en cours
    synth.current.cancel();

    try {
      // Crer un texte  partir des informations du ticket
      const textToSpeak = ` Le ticket numro ${nextPatient.STR_TICNAME}. pour une opration de ${nextPatient.LG_LSTOPEID}. est appel a la caisse numro 1`;

      // Crer un nouvel objet SpeechSynthesisUtterance
      const utterance = new SpeechSynthesisUtterance(textToSpeak);

      // Dfinir la langue
      utterance.lang = "fr-FR";

      // Ralentir la vitesse de lecture
      utterance.rate = 0.9;

      // Dfinir la voix si une est slectionne
      if (voiceRef.current) {
        utterance.voice = voiceRef.current;
      }

      // vnements pour le dbogage
      utterance.onstart = () => console.log("Dbut de l'annonce vocale");
      utterance.onend = () => console.log("Fin de l'annonce vocale");
      utterance.onerror = (e) => console.error("Erreur d'annonce vocale:", e);

      // Lire le texte
      synth.current.speak(utterance);
    } catch (error) {
      console.error("Erreur lors de la lecture vocale:", error);
    }
  };

  // Effectuer les lectures vocales ds que le composant est mont et que les voix sont charges
  useEffect(() => {
    if (nextPatient && voicesLoaded) {
      // Premire lecture immdiate ds que les voix sont charges
      speakTicketInfo();

      // Deuxime lecture aprs 10 secondes
      const timer = setTimeout(() => {
        speakTicketInfo();
      }, 10000);

      return () => {
        clearTimeout(timer);
        if (synth.current) {
          synth.current.cancel(); // Arrter la synthse vocale si le composant est dmont
        }
      };
    }
  }, [nextPatient, voicesLoaded]); // Dpendance  voicesLoaded pour s'assurer que les voix sont prtes

  return (
    <Panel bordered className="notification-bar">
      <FlexboxGrid justify="space-between" align="middle" className="mb-10">
        <FlexboxGrid.Item>
          <h4>Prochain patient</h4>
        </FlexboxGrid.Item>
        <FlexboxGrid.Item>
          <IconButton
            icon={<X size={16} />}
            size="xs"
            appearance="subtle"
            onClick={() => setShowNotification(false)}
          />
        </FlexboxGrid.Item>
      </FlexboxGrid>
      <FlexboxGrid align="middle">
        <FlexboxGrid.Item className="mr-15">
          <div className="notification-icon">
            <Bell size={18} color="#1c64f2" />
          </div>
        </FlexboxGrid.Item>
        <FlexboxGrid.Item>
          <p className="notification-name">{nextPatient.name}</p>
          <p className="notification-room">{nextPatient.room}</p>
        </FlexboxGrid.Item>
      </FlexboxGrid>
    </Panel>
  );
}

export default NotificationBar;
