import React, { useState, useEffect } from "react";
import {
  Steps,
  Panel,
  Loader,
  Animation,
  Modal,
  Button,
  useMediaQuery,
} from "rsuite";
import "rsuite/dist/rsuite.min.css";
import "../Wizard.css";
import WizardNavbar from "../WizardNavbar";
import WizardFooter from "../WizardFooter";
import ServiceSelection from "./steps/ServiceSelection";
import OperationSelection from "./steps/OperationSelection";
import PrestationSelection from "./steps/PrestationSelection";
import PriorisationCodeStep from "./steps/PriorisationCodeStep"; // Nouveau composant
import SatisfactionFeedback from "./steps/SatisfactionFeedback"; // Nouveau composant
import UserInfoForm from "./steps/UserInfoForm";
import SummaryStep from "./steps/SummaryStep";
import TicketSuccess from "./steps/TicketSuccess";
import SuccessModal from "./steps/SuccessModal";
import { submitTicketRequest } from "./steps/ticketApi";
import useFetchData from "../../../services/useFetchData";
import usePostData from "../../../services/usePostData";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

// Utiliser des emojis au lieu d'icnes (conserv comme fallback)
export const serviceIcons = {
  information: "",
  consultation: "",
  paiement: "",
  reclamation: "",
  livraison: "",
  reparation: "",
};

// Couleurs pour les services et oprations ( associer dynamiquement avec les donnes de l'API)
const serviceColors = [
  "#3498db",
  "#9b59b6",
  "#2ecc71",
  "#e74c3c",
  "#f39c12",
  "#1abc9c",
];

const { Fade } = Animation;

const VerticalTicketWizard = () => {
  // tats principaux
  const [step, setStep] = useState(0);
  const [selectedService, setSelectedService] = useState(null);
  const [selectedOperation, setSelectedOperation] = useState(null);
  const [selectedPrestation, setSelectedPrestation] = useState(null);
  const [priorisationCode, setPriorisationCode] = useState(""); // Nouvel tat pour le code de priorisation
  const [selectedEmotion, setSelectedEmotion] = useState(null); // Nouvel tat pour l'moticne slectionne
  const [userData, setUserData] = useState({
    name: "",
    phone: "",
    email: "",
  });
  const [ticketNumber, setTicketNumber] = useState("");
  const [waitTime, setWaitTime] = useState(0);
  const [isLoading, setIsLoading] = useState(false);
  const [showSuccess, setShowSuccess] = useState(false);
  const [validationErrors, setValidationErrors] = useState({});
  const [selectionDelay, setSelectionDelay] = useState(false);
  const [operationDelay, setOperationDelay] = useState(false);
  const [apiSubmitting, setApiSubmitting] = useState(false);
  // const [apiError, setApiError] = useState(null);
  const [services, setServices] = useState([]);
  const [operations, setOperations] = useState([]);

  const {
    postData,
    loading,
    error: apiError,
  } = usePostData("TicketManager/createTicket");

  // Paramtres pour la requte des services
  const serviceParams = {
    LG_TYLID: "SERVICE",
    mode: "listServicebyagence",
    order: "t.STR_LSTOTHERVALUE2",
    LG_AGEID: "004",
  };

  // Utilisation du hook pour rcuprer les services
  const {
    data: fetchedServiceData,
    error: fetchServiceError,
    loading: fetchServiceLoading,
    refetch: refetchServices,
  } = useFetchData(
    "ConfigurationManager/listServicebyagence",
    serviceParams,
    "data"
  );

  // Paramtres pour la requte des oprations (avec selectedService comme dpendance)
  const operationParams = {
    search_value: "",
    LG_TYLID: "PRODUIT",
    STR_LSTOTHERVALUE: selectedService,
    mode: "listAgenceListe",
    order: "t.lgLstid.strLstothervalue2",
    LG_AGEID: "004",
  };

  // Hook pour rcuprer les oprations pour le service slectionn
  const {
    data: fetchedOperationData,
    error: fetchOperationError,
    loading: fetchOperationLoading,
    refetch: refetchOperations,
  } = useFetchData(
    "ConfigurationManager/listAgenceListe",
    selectedService ? operationParams : null, // On passe null si selectedService est null
    "data"
  );

  // Convertir les donnes de services de l'API en format utilisable
  useEffect(() => {
    if (fetchedServiceData && Array.isArray(fetchedServiceData)) {
      const mappedServices = fetchedServiceData.map((item, index) => ({
        id: item.LG_LSTID,
        name: item.STR_LSTVALUE,
        description: item.STR_LSTDESCRIPTION,
        color: serviceColors[index % serviceColors.length], // Attribuer une couleur depuis la liste
        imagePath: `assets/images/${
          item.STR_FOLDER
        }${item.STR_IMAGELISTE.trim()}`,
        iconName: item.STR_IMAGELISTE,
      }));
      setServices(mappedServices);
    }
  }, [fetchedServiceData]);

  // Convertir les donnes d'oprations de l'API en format utilisable
  useEffect(() => {
    if (fetchedOperationData && Array.isArray(fetchedOperationData)) {
      const mappedOperations = fetchedOperationData.map((item, index) => ({
        id: item.LG_LSTID,
        name: item.STR_LSTVALUE,
        description: item.STR_LSTDESCRIPTION,
        color: serviceColors[index % serviceColors.length], // Attribuer une couleur depuis la liste
        imagePath: `assets/images/${
          item.STR_FOLDER
        }${item.STR_IMAGELISTE.trim()}`,
        iconName: item.STR_IMAGELISTE,
        serviceId: item.STR_LSTOTHERVALUE, // ID du service parent
      }));
      setOperations(mappedOperations);
    }
  }, [fetchedOperationData]);

  // Media queries pour le responsive design
  const isMobile = useMediaQuery("(max-width: 767px)");
  const isExtraSmall = useMediaQuery("(max-width: 480px)");

  useEffect(() => {
    // Gnrer un numro de ticket alatoire mais consistant
    setTicketNumber(`AFG-${Math.floor(Math.random() * 1000)}`);
  }, []);

  useEffect(() => {
    // Gnrer un numro de ticket alatoire quand on arrive  l'tape finale
    if (step === 5) {
      const randomTicket = `A-${Math.floor(Math.random() * 100)}`;
      const randomWait = Math.floor(Math.random() * 20) + 5; // Entre 5 et 25 minutes
      setTicketNumber(randomTicket);
      setWaitTime(randomWait);
    }
  }, [step]);

  // Effet pour la redirection automatique aprs le modal de succs
  useEffect(() => {
    let timer;
    if (showSuccess) {
      timer = setTimeout(() => {
        handleReset();
      }, 2000);
    }
    return () => {
      if (timer) clearTimeout(timer);
    };
  }, [showSuccess]);

  // Fonctions utilitaires pour obtenir les dtails du service et de l'opration slectionns
  const getSelectedServiceData = () => {
    return services.find((s) => s.id === selectedService) || {};
  };

  const getSelectedOperationData = () => {
    return operations.find((o) => o.id === selectedOperation) || {};
  };

  // Handler pour la saisie du code de priorisation
  const handlePriorisationCodeChange = (code) => {
    setPriorisationCode(code);
  };

  // Handler pour la slection de l'moticne
  const handleEmotionSelect = (emotion) => {
    setSelectedEmotion(emotion);
    // Aprs slection de l'moticne, passer  l'tape suivante
    setSelectionDelay(true);
    setTimeout(() => {
      setSelectionDelay(false);
      setTimeout(() => {
        setIsLoading(false);
        setStep(3); // Aller  l'tape UserInfoForm
      }, 100);
    }, 700);
  };

  // Handler pour la validation du code de priorisation
  const handlePriorisationCodeSubmit = () => {
    if (priorisationCode.trim() !== "") {
      setSelectionDelay(true);
      setTimeout(() => {
        setSelectionDelay(false);
        setTimeout(() => {
          setIsLoading(false);
          setStep(3); // Aller  l'tape UserInfoForm
        }, 100);
      }, 700);
    } else {
      // Grer le cas o le code est vide
      setValidationErrors({
        ...validationErrors,
        priorisationCode: "Veuillez entrer un code de priorisation valide",
      });
    }
  };

  // Gestionnaire pour la slection des prestations
  const handlePrestationSelect = (prestationId) => {
    setSelectedPrestation(prestationId);
    setSelectionDelay(true);

    setTimeout(() => {
      setSelectionDelay(false);
      setTimeout(() => {
        setIsLoading(false);
        // Dterminer la prochaine tape selon la prestation
        if (prestationId === "Service") {
          setStep(1); // Aller  l'tape Service
        } else if (prestationId === "Priorisation") {
          setStep(1.5); // Aller  l'tape de saisie du code de priorisation
        } else if (prestationId === "Satisfaction") {
          setStep(1.75); // Aller  l'tape des moticnes
        }
      }, 100);
    }, 700);
  };

  // Gestionnaires d'vnements
  const handleServiceSelect = (serviceId) => {
    setSelectedService(serviceId);
    setSelectedOperation(null); // Rinitialiser l'opration slectionne
    setSelectionDelay(true);

    setTimeout(() => {
      setSelectionDelay(false);
      setTimeout(() => {
        setIsLoading(false);
        setStep(2); // Aller  OperationSelection aprs ServiceSelection
      }, 100);
    }, 700);
  };

  const handleOperationSelect = (operationId) => {
    setSelectedOperation(operationId);
    setOperationDelay(true);

    setTimeout(() => {
      setOperationDelay(false);
      setTimeout(() => {
        setIsLoading(false);
        setStep(3); // Toujours aller  UserInfoForm aprs OperationSelection
      }, 100);
    }, 700);
  };

  const handleUserDataChange = (value, name) => {
    setUserData({
      ...userData,
      [name]: value,
    });

    // Effacer l'erreur lorsque l'utilisateur corrige le champ
    if (validationErrors[name]) {
      setValidationErrors({
        ...validationErrors,
        [name]: null,
      });
    }
  };

  const handleNext = async () => {
    // Si on est  l'tape du formulaire utilisateur, aller  l'tape rcapitulative
    if (step === 3) {
      setIsLoading(true);
      setTimeout(() => {
        setIsLoading(false);
        setStep(4); // Aller  SummaryStep
      }, 400);
    }
    // Si on est  l'tape rcapitulative, soumettre le formulaire  l'API
    else if (step === 4) {
      try {
        const requestData = {
          LG_LSTID: selectedOperation,
          mode: "createTicket",
          STR_TICCODEPRIORITE: priorisationCode || "",
          STR_TICPHONE: userData.phone || "0749345289",
          STR_UTITOKEN: "",
          LG_AGEID: "004",
          LG_SOCID: "",
        };

        // Animation de transition
        setIsLoading(true);
        setTimeout(() => {
          setIsLoading(false);
          setStep(5); // Aller  TicketSuccess
        }, 400);
      } catch (error) {
        setApiSubmitting(false);
        console.error("API error:", error);
      }
    } else {
      // Animation de transition normale pour les autres tapes
      setIsLoading(true);
      setTimeout(() => {
        setIsLoading(false);
        setStep(step + 1);
      }, 400);
    }
  };

  const handlePrevious = () => {
    setIsLoading(true);
    setTimeout(() => {
      setIsLoading(false);
      
      // Logique de navigation en arrire en fonction de la prestation et de l'tape actuelle
      if (step === 3) {
        if (selectedPrestation === "Service") {
          setStep(2); // Retour  OperationSelection
        } else if (selectedPrestation === "Priorisation") {
          setStep(1.5); // Retour  l'tape de saisie du code de priorisation
        } else if (selectedPrestation === "Satisfaction") {
          setStep(1.75); // Retour  l'tape des moticnes
        }
      } else if (step === 2) {
        setStep(1); // Retour  ServiceSelection
      } else if (step === 1.5 || step === 1.75) {
        setStep(0); // Retour  PrestationSelection
      } else if (step === 1) {
        setStep(0); // Retour  PrestationSelection
      } else if (step === 4) {
        setStep(3); // Retour  UserInfoForm
      } else {
        setStep(Math.max(0, step - 1));
      }
    }, 400);
  };

  const handleConfirm = () => {
    setShowSuccess(true);
  };

  const handleReset = () => {
    // Rinitialiser tout le wizard
    setShowSuccess(false);
    setStep(0);
    setSelectedService(null);
    setSelectedOperation(null);
    setSelectedPrestation(null);
    setPriorisationCode("");
    setSelectedEmotion(null);
    setUserData({ name: "", phone: "", email: "" });
    setValidationErrors({});
  };

  const validateUserData = () => {
    return userData.name.trim() !== "" && userData.phone.trim() !== "";
  };

  // Fonction pour obtenir la couleur de la prestation slectionne
  const getSelectedPrestationColor = () => {
    if (selectedPrestation === "Service") return "#3498db";
    if (selectedPrestation === "Priorisation") return "#9b59b6";
    if (selectedPrestation === "Satisfaction") return "#2ecc71";
    return "";
  };

  // Rendu du contenu en fonction de l'tape actuelle
  const renderStepContent = () => {
    // Gestion du chargement initial des services
    if (fetchServiceLoading && step === 0) {
      return (
        <div className="loading-container">
          <Loader size="lg" content="Chargement des services..." vertical />
        </div>
      );
    }

    // Gestion du chargement des oprations
    if (fetchOperationLoading && step === 2) {
      return (
        <div className="loading-container">
          <Loader size="lg" content="Chargement des oprations..." vertical />
        </div>
      );
    }

    // Gestion du chargement des transitions
    if (isLoading) {
      return (
        <div className="loading-container">
          <Loader size="lg" content="Chargement..." vertical />
        </div>
      );
    }

    // Gestion des erreurs de chargement des services
    if (fetchServiceError && step === 0) {
      return (
        <div className="error-container">
          <div className="error-card">
            <div className="error-icon-wrapper">
              <svg
                className="error-icon"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <circle
                  cx="12"
                  cy="12"
                  r="10"
                  stroke="currentColor"
                  strokeWidth="1.5"
                />
                <path
                  d="M12 7V13"
                  stroke="currentColor"
                  strokeWidth="2"
                  strokeLinecap="round"
                />
                <circle cx="12" cy="16" r="1" fill="currentColor" />
              </svg>
            </div>

            <div className="error-content">
              <h3 className="error-title">Impossible de charger les donnes</h3>
              <p className="error-message">
                Un problme est survenu lors du chargement des services.
                Veuillez ressayer ou contacter l'assistance si le problme
                persiste.
              </p>
            </div>

            <div className="error-actions">
              <button className="retry-button" onClick={refetchServices}>
                <span className="retry-icon">
                  <svg
                    viewBox="0 0 24 24"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M17.65 6.35C16.2 4.9 14.21 4 12 4C7.58 4 4.01 7.58 4.01 12C4.01 16.42 7.58 20 12 20C15.73 20 18.84 17.45 19.73 14H17.65C16.83 16.33 14.61 18 12 18C8.69 18 6 15.31 6 12C6 8.69 8.69 6 12 6C13.66 6 15.14 6.69 16.22 7.78L13 11H20V4L17.65 6.35Z"
                      fill="currentColor"
                    />
                  </svg>
                </span>
                Ressayer
              </button>
            </div>
          </div>
        </div>
      );
    }

    // Gestion des erreurs de chargement des oprations
    if (fetchOperationError && step === 2) {
      return (
        <div className="error-container">
          <p className="error-message">
            Erreur lors du chargement des oprations. Veuillez ressayer.
          </p>
          <Button onClick={refetchOperations} appearance="primary">
            Ressayer
          </Button>
        </div>
      );
    }

    switch (step) {
      case 0:
        return (
          <Fade in={true}>
            <PrestationSelection
              selectedService={selectedPrestation}
              selectionDelay={selectionDelay}
              handleServiceSelect={handlePrestationSelect}
              handleNext={handleNext}
              getSelectedServiceColor={getSelectedPrestationColor}
              isMobile={isMobile}
            />
          </Fade>
        );

      case 1:
        // Cette tape n'est accessible que pour la prestation "Service"
        return (
          <Fade in={true}>
            <ServiceSelection
              services={services}
              selectedService={selectedService}
              selectionDelay={selectionDelay}
              handleServiceSelect={handleServiceSelect}
              handleNext={handleNext}
              getSelectedServiceColor={() =>
                getSelectedServiceData().color || ""
              }
              isMobile={isMobile}
              handlePrevious={handlePrevious}
            />
          </Fade>
        );

      case 1.5:
        // tape de saisie du code de priorisation
        return (
          <Fade in={true}>
            <PriorisationCodeStep
              priorisationCode={priorisationCode}
              handlePriorisationCodeChange={handlePriorisationCodeChange}
              handlePriorisationCodeSubmit={handlePriorisationCodeSubmit}
              handlePrevious={handlePrevious}
              validationErrors={validationErrors}
              getSelectedPrestationColor={getSelectedPrestationColor}
              isMobile={isMobile}
            />
          </Fade>
        );

      case 1.75:
        // tape de slection des moticnes
        return (
          <Fade in={true}>
            <SatisfactionFeedback
              selectedEmotion={selectedEmotion}
              handleEmotionSelect={handleEmotionSelect}
              handlePrevious={handlePrevious}
              getSelectedPrestationColor={getSelectedPrestationColor}
              isMobile={isMobile}
            />
          </Fade>
        );

      case 2:
        // OperationSelection est accessible pour les prestations "Service"
        return (
          <Fade in={true}>
            <OperationSelection
              operations={operations}
              selectedOperation={selectedOperation}
              operationDelay={operationDelay}
              selectedService={selectedService}
              handleOperationSelect={handleOperationSelect}
              handlePrevious={handlePrevious}
              handleNext={handleNext}
              getSelectedServiceName={() => getSelectedServiceData().name || ""}
              getSelectedOperationColor={() =>
                getSelectedOperationData().color || ""
              }
              serviceIcons={serviceIcons}
              isMobile={isMobile}
              getSelectedServiceData={getSelectedServiceData}
            />
          </Fade>
        );

      case 3:
        return (
          <Fade in={true}>
            <UserInfoForm
              userData={userData}
              validationErrors={validationErrors}
              handleUserDataChange={handleUserDataChange}
              handlePrevious={handlePrevious}
              handleNext={handleNext}
              validateUserData={validateUserData}
              selectedService={selectedService}
              getSelectedServiceName={() => {
                if (selectedPrestation === "Service") {
                  return getSelectedServiceData().name || "";
                } else if (selectedPrestation === "Priorisation") {
                  return "Priorisation";
                } else if (selectedPrestation === "Satisfaction") {
                  return "Satisfaction";
                }
                return "";
              }}
              getSelectedServiceColor={() => {
                if (selectedPrestation === "Service") {
                  return getSelectedServiceData().color || "";
                } else if (selectedPrestation === "Priorisation") {
                  return "#9b59b6";
                } else if (selectedPrestation === "Satisfaction") {
                  return "#2ecc71";
                }
                return "";
              }}
              serviceIcons={serviceIcons}
            />
          </Fade>
        );

      case 4:
        return (
          <Fade in={true}>
            <SummaryStep
              userData={userData}
              selectedService={selectedService}
              selectedOperation={selectedOperation}
              getSelectedServiceName={() => {
                if (selectedPrestation === "Service") {
                  return getSelectedServiceData().name || "";
                } else if (selectedPrestation === "Priorisation") {
                  return "Priorisation";
                } else if (selectedPrestation === "Satisfaction") {
                  return "Satisfaction";
                }
                return "";
              }}
              getSelectedServiceColor={() => {
                if (selectedPrestation === "Service") {
                  return getSelectedServiceData().color || "";
                } else if (selectedPrestation === "Priorisation") {
                  return "#9b59b6";
                } else if (selectedPrestation === "Satisfaction") {
                  return "#2ecc71";
                }
                return "";
              }}
              getSelectedOperationName={() => {
                if (selectedPrestation === "Service") {
                  return getSelectedOperationData().name || "";
                } else if (selectedPrestation === "Priorisation") {
                  return "Rencontrer un conseiller";
                } else if (selectedPrestation === "Satisfaction") {
                  return selectedEmotion ? `Feedback: ${selectedEmotion}` : "Feedback";
                }
                return "";
              }}
              getSelectedOperationColor={() => {
                if (selectedPrestation === "Service") {
                  return getSelectedOperationData().color || "";
                } else if (selectedPrestation === "Priorisation") {
                  return "#9b59b6";
                } else if (selectedPrestation === "Satisfaction") {
                  return "#2ecc71";
                }
                return "";
              }}
              handlePrevious={handlePrevious}
              handleNext={handleNext}
              serviceIcons={serviceIcons}
              apiSubmitting={apiSubmitting}
              apiError={apiError}
              selectedPrestation={selectedPrestation}
              priorisationCode={priorisationCode}
              selectedEmotion={selectedEmotion}
            />
          </Fade>
        );

      case 5:
        return (
          <Fade in={true}>
            <TicketSuccess
              ticketNumber={ticketNumber}
              userData={userData}
              getSelectedServiceName={() => {
                if (selectedPrestation === "Service") {
                  return getSelectedServiceData().name || "";
                } else if (selectedPrestation === "Priorisation") {
                  return "Priorisation";
                } else if (selectedPrestation === "Satisfaction") {
                  return "Satisfaction";
                }
                return "";
              }}
              getSelectedOperationName={() => {
                if (selectedPrestation === "Service") {
                  return getSelectedOperationData().name || "";
                } else if (selectedPrestation === "Priorisation") {
                  return "Rencontrer un conseiller";
                } else if (selectedPrestation === "Satisfaction") {
                  return selectedEmotion ? `Feedback: ${selectedEmotion}` : "Feedback";
                }
                return "";
              }}
              handleConfirm={handleConfirm}
            />
          </Fade>
        );

      default:
        return null;
    }
  };

  // Dterminer les tapes  afficher en fonction de la prestation slectionne
  const renderSteps = () => {
    const stepsArr = [];
    
    // tape de prestation commune  tous les parcours
    stepsArr.push(
      <Steps.Item
        key="prestation"
        title="Prestation"
        description={isMobile ? "" : "Choisissez votre prestation"}
      />
    );

    // tapes conditionnelles en fonction de la prestation slectionne
    if (selectedPrestation === "Service") {
      stepsArr.push(
        <Steps.Item
          key="service"
          title="Service"
          description={isMobile ? "" : "Choisissez votre service"}
        />
      );
      stepsArr.push(
        <Steps.Item
          key="operations"
          title="Oprations"
          description={isMobile ? "" : "Type d'opration"}
        />
      );
    } else if (selectedPrestation === "Priorisation") {
      stepsArr.push(
        <Steps.Item
          key="priorisation"
          title="Code"
          description={isMobile ? "" : "Code de priorisation"}
        />
      );
    } else if (selectedPrestation === "Satisfaction") {
      stepsArr.push(
        <Steps.Item
          key="satisfaction"
          title="Feedback"
          description={isMobile ? "" : "Votre satisfaction"}
        />
      );
    }

    // tapes communes  tous les parcours
    stepsArr.push(
      <Steps.Item
        key="infos"
        title="Infos"
        description={isMobile ? "" : "Vos coordonnes"}
      />
    );

    stepsArr.push(
      <Steps.Item
        key="resume"
        title="Rsum"
        description={isMobile ? "" : "Vrifiez vos informations"}
      />
    );

    stepsArr.push(
      <Steps.Item
        key="termine"
        title="Termin"
        description={isMobile ? "" : "Rcuprez votre ticket"}
      />
    );

    return stepsArr;
  };

  // Calcul de l'tape courante pour le composant Steps
  const calculateStepIndex = () => {
    if (step === 0) return 0;
    if (step === 1) return 1;
    if (step === 1.5) return 1;
    if (step === 1.75) return 1;
    if (step === 2) return selectedPrestation === "Service" ? 2 : 1;
    if (step === 3) {
      if (selectedPrestation === "Service") return 3;
      else return 2;
    }
    if (step === 4) {
      if (selectedPrestation === "Service") return 4;
      else return 3;
    }
    if (step === 5) {
      if (selectedPrestation === "Service") return 5;
      else return 4;
    }
    return step;
  };

  return (
    <div className="ticket-wizard-container m-20">
      {/* En-tte fixe en haut de l'cran */}
      <div className="fixed-header">
        <WizardNavbar />
        <div className="wizard-steps-container">
          <div className="wizard-steps-card rounded-bottom">
            <Steps current={calculateStepIndex()} vertical={!isMobile} small={isExtraSmall}>
              {renderSteps()}
            </Steps>
          </div>
        </div>
      </div>

      {/* Contenu principal scrollable qui occupe tout l'espace disponible */}
      <div className="main-content-area">
        <Panel className="wizard-panel ">
          {renderStepContent()}
          <ToastContainer
            position="top-right"
            autoClose={5000}
            hideProgressBar={false}
          />
        </Panel>
      </div>

      {/* Footer fixe en bas de l'cran */}
      <div className="fixed-footer">
        <WizardFooter />
      </div>

      {/* Modal de confirmation */}
      <SuccessModal showSuccess={showSuccess} handleReset={handleReset} />
    </div>
  );
};

export default VerticalTicketWizard;