import { useState, useEffect } from "react";
import { urlBaseImage, rootUrl } from './urlUtils';
import axios from "axios";
import { useNavigate } from "react-router-dom";

/**
 * Hook pour rcuprer des donnes d'API avec capacit de refetch
 * @param {string} url - URL de l'API
 * @param {Object} params - Paramtres pour la requte
 * @param {string} dataKey - Cl pour accder aux donnes dans la rponse
 * @param {number} refreshTrigger - Dclencheur pour rafrachir les donnes
 * @returns {Object} Les donnes rcupres, l'tat du chargement, les erreurs et la fonction refetch
 */
const useFetchData = (apiEndPoint, params, dataKey, refreshTrigger = 0) => {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  // Fonction pour vrifier si un objet contient des valeurs null ou undefined
  const validateParams = (params) => {
    if (!params) return false;

    // Crer une copie propre des paramtres en supprimant les valeurs null/undefined
    const cleanParams = {};
    let isValid = false;

    for (const key in params) {
      if (params[key] !== null && params[key] !== undefined) {
        cleanParams[key] = params[key];
        isValid = true; // Au moins un paramtre valide
      }
    }

    return { isValid, cleanParams };
  };

  // Fonction pour rcuprer les donnes
  const fetchData = async () => {
    try {
      setLoading(true);

      // Valider les paramtres avant d'envoyer la requte
      const { isValid, cleanParams } = validateParams(params);

      if (!isValid) {
        throw new Error("Paramtres invalides: tous les paramtres sont null ou undefined");
      }

      const response = await axios.post(
        rootUrl + apiEndPoint,
        cleanParams,
        { headers: { 'Content-Type': 'application/x-www-form-urlencoded' } }
      );

      if (response.data.code_statut == 2) {
        return navigate(process.env.REACT_APP_SIGN_IN);
      }

      const fetchedData = dataKey ? response.data[dataKey] : response.data;
      setData(fetchedData);
      setError(null);
      return fetchedData;
    } catch (err) {
      setError(err);
      console.error("Erreur lors de la rcupration des donnes:", err);
      return null;
    } finally {
      setLoading(false);
    }
  };

  // Fonction de re-fetch expose
  const refetch = async () => {
    return await fetchData();
  };

  // Effet pour rcuprer les donnes au chargement ou quand refreshTrigger change
  useEffect(() => {
    fetchData();
  }, [rootUrl + apiEndPoint, JSON.stringify(params), refreshTrigger]); // Inclure refreshTrigger dans les dpendances

  return { data, loading, error, refetch };
};

export default useFetchData;