import React, { useState, useEffect } from "react";
import { Panel, List, FlexboxGrid } from "rsuite";
import "./queueStyles.css"; // Importation des styles externaliss
import useFetchData from "../../../services/useFetchData";
import NotificationBar from "./NotificationBar"; // Importation du composant NotificationBar

function QueueList({ queueItems: initialQueueItems }) {
  const [queueItems, setQueueItems] = useState(initialQueueItems || []);
  const [showNotification, setShowNotification] = useState(false);
  const [nextPatient, setNextPatient] = useState(null);
  const [processedTickets, setProcessedTickets] = useState(new Set());
  const MAX_ITEMS = 8; // Constante pour limiter le nombre d'lments affichs

  // Fonction pour obtenir la couleur base sur le type
  const getTypeColor = (type) => {
    switch (type) {
      case "urgent":
        return "red";
      case "premium":
        return "violet";
      default:
        return "blue";
    }
  };

  const getStatusColor = (status) => {
    switch (status) {
      case "En cours":
        return "green";
      case "Prparation":
        return "orange";
      default:
        return "gray";
    }
  };

  const serviceParams = {
    search_value: "",
    mode: "readTicket",
    LG_AGEID: "004",
    LG_LSTID: "12926453248142854859",
  };

  // Utilisation du hook pour rcuprer les services
  const {
    data: readTicketData,
    error: fetchServiceError,
    loading: fetchServiceLoading,
    refetch: refetchServices,
  } = useFetchData("TicketManager/readTicket", serviceParams);

  // Effet pour vrifier et ajouter les nouveaux tickets
  useEffect(() => {
    // Si des donnes sont reues et que le code_statut est gal  1, on ajoute le ticket  la liste
    if (readTicketData && readTicketData.code_statut === "1") {
      const ticketId = readTicketData.STR_TICNAME;
      
      // Vrifier que le ticket est valide et n'a pas dj t trait
      if (ticketId && ticketId !== "--" && !processedTickets.has(ticketId)) {
        // Cration d'un nouvel item format pour correspondre  la structure attendue
        const newTicket = {
          STR_TICNAME: ticketId,
          name: `${readTicketData.LG_LSTOPEID}`,
          LG_LSTOPEID: readTicketData.LG_LSTOPEID,
          LG_PAPID: readTicketData.LG_PAPID,
          desc_statut:readTicketData.desc_statut,
          estimatedTime: 5, // Vous pouvez ajuster cette valeur selon vos besoins
          type: "normal", // Vous pouvez dfinir le type en fonction de vos besoins
        };

        console.log("Nouveau ticket dtect:", newTicket);
        
        // Ajouter  la liste des tickets traits
        setProcessedTickets(prev => new Set(prev).add(ticketId));
        
        // Ajouter le nouveau ticket au DBUT de la liste et limiter  MAX_ITEMS
        setQueueItems(prevItems => {
          const updatedItems = [newTicket, ...prevItems].slice(0, MAX_ITEMS);
          return updatedItems;
        });
        
        // Dclencher la notification de manire fiable
        setTimeout(() => {
          setNextPatient(newTicket);
          setShowNotification(true);
        }, 300); // Petit dlai pour s'assurer que l'tat est mis  jour
      }
    }
  }, [readTicketData]);

  // Polling toutes les 5 secondes (augmentation de 3  5 pour rduire la charge)
  useEffect(() => {
    console.log("Initialisation du polling des tickets...");
    
    // Appel immdiat aprs un court dlai pour permettre au composant de se monter compltement
    const initialTimeout = setTimeout(() => {
      refetchServices();
    }, 500);

    // Configuration de l'intervalle (toutes les 5 secondes)
    const intervalId = setInterval(refetchServices, 5000);

    // Nettoyage  la destruction du composant
    return () => {
      clearTimeout(initialTimeout);
      clearInterval(intervalId);
      console.log("Polling arrt");
    };
  }, []); // Tableau de dpendances vide pour ne s'excuter qu'une seule fois au montage

  // Masquer automatiquement la notification aprs 20 secondes (augment pour donner plus de temps  la synthse vocale)
  useEffect(() => {
    if (showNotification) {
      const timeoutId = setTimeout(() => {
        setShowNotification(false);
      }, 20000);

      return () => clearTimeout(timeoutId);
    }
  }, [showNotification]);



//   LG_LSTID
// : 
// "Ticket appel"
// LG_LSTOPEID
// : 
// "Souscription aux produits dassurance"
// LG_PAPID
// : 
// "Caisse 1"
// STR_TICNAME
// : 
// "SOU-007"
// code_statut
// : 
// "1"
// desc_statut
// : 
// "Le ticket numro SOU 7 est appel A la Caisse 1"
// translate
// : 
// "tikiti nimoro S O U - fou fou wolovila  bi wele ka taa jatboyoro la C a i s s e   kel "

  return (
    <>
      {showNotification && nextPatient && (
        <NotificationBar
          nextPatient={nextPatient}
          setShowNotification={setShowNotification}
        />
      )}

      <Panel bodyFill className="queue-panel">
        <div className="queue-header">
          <h2 className="queue-title">File d'attente actuelle</h2>
          <p className="queue-subtitle">Mise  jour en temps rel</p>
        </div>

        <div className="queue-list-container">
          <List>
            {queueItems.map((item, index) => (
              <List.Item key={item.STR_TICNAME}>
                <Panel
                  bordered
                  className={`queue-item ${index === 0 ? "active" : ""}`}
                  style={{
                    borderLeftColor: getTypeColor(item.type),
                  }}
                >
                  <FlexboxGrid justify="space-between" align="middle">
                    <FlexboxGrid.Item>
                      <FlexboxGrid align="middle">
                        <FlexboxGrid.Item className="mr-15">
                          <div
                            className={`queue-number ${
                              index === 0 ? "active" : ""
                            }`}
                          >
                            <img src="assets/images/logos/cartes-bancaires.svg" alt="Icne Operations de caisse" width="30" height="30" class="rounded-circle"/>
                            {/* {item.STR_TICNAME} */}
                          </div>
                        </FlexboxGrid.Item>

                        <FlexboxGrid.Item>
                          <div className="queue-name">{item.STR_TICNAME}</div>
                          <div
                            className={`queue-status ${getStatusColor(
                              item.LG_LSTOPEID
                            )}`}
                          >
                            {item.LG_LSTOPEID}
                          </div>
                        </FlexboxGrid.Item>
                      </FlexboxGrid>
                    </FlexboxGrid.Item>

                    <FlexboxGrid.Item className="text-right">
                      <div className="queue-room">{item.LG_PAPID}</div>
                      {/* <div className="queue-time">{item.estimatedTime} min</div> */}
                    </FlexboxGrid.Item>
                  </FlexboxGrid>
                </Panel>
              </List.Item>
            ))}
          </List>
        </div>
      </Panel>
    </>
  );
}

export default QueueList;